                this.$msgbox({
                    title: '提示',
                    message: "确定要删除【xxxx】吗？",
                    showCancelButton: true,
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                }).then(action => {
                    console.log(2)
                }).catch(()=>{});
            },
        }
    }
</script>
<style lang="less" scoped>
    .main{
        .el-tabs{
            height: 100%;
        }
        .tab-item{
            display: block;
            width: 100px;
            text-align: center;
        }
    }
    .query-form{
        .el-input, .el-select, .el-date-picker{
            width: 250px;
            margin-right: 30px;
        }
    }   
    .dialogReceipt, .dialogDeploy, .dialogPaymentPlan, .dialogPaymentRecord, .dialogReceiptRecord{
        .el-input, .el-select, .el-textarea{
            width: 330px;
        }
    }
</style>



